import React, { ChangeEvent, Component, MouseEvent } from 'react';
import { css, StyleSheet } from 'aphrodite';

interface MenuProps {
  open: boolean;
  onSave: (name: string) => void;
  onClose: (evt: MouseEvent<HTMLDivElement> | MouseEvent<HTMLButtonElement>) => void;
}

interface State {
  inputValue: string;
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: 'rgba(0,0,0,0.3)',
    top: 0,
    bottom: 0,
    left: 0,
    right: 0,
    position: 'fixed',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 9999,
  },
  form: {
    display: 'flex',
    listStyle: 'none',
    padding: 0,
    boxShadow: 'rgba(0,0,0,0.3) 5px 5px 20px 1px',
  },
  input: {
    border: 'none',
    padding: '1em',
    width: '20vw',
  },
  button: {
    backgroundColor: '#333',
    color: '#fff',
    cursor: 'pointer',
    display: 'inline-block',
    border: 'none',
    textAlign: 'center',
    fontSize: '1.55em',
  },
  addBtn: {
    backgroundColor: 'green',
  },
  cancelBtn: {
    backgroundColor: '#999',
  }
});

class SaveStoreInput extends Component<MenuProps, State> {
  state = {
    inputValue: '',
  };

  handleChange = (evt: ChangeEvent<HTMLInputElement>) => {
    this.setState({
      inputValue: evt.target.value,
    });
  };

  handleSave = (evt: any) => {
    evt.preventDefault();
    this.props.onSave(this.state.inputValue);
  };

  render() {
    return this.props.open ? (
      <div className={css(styles.container)} onClick={this.props.onClose}>
        <form className={css(styles.form)} onSubmit={this.handleSave}>
          <input
            className={css(styles.input)}
            placeholder='Name your resume...'
            value={this.state.inputValue}
            onChange={this.handleChange}
            required={true}
          />
          <button type='button' className={css(styles.button, styles.addBtn)} onClick={this.handleSave}>
            <i className='fas fa-save' />
          </button>
          <button type='button' className={css(styles.button, styles.cancelBtn)} onClick={this.props.onClose}>
            <i className='fas fa-window-close' onClick={this.props.onClose} />
          </button>
        </form>
      </div>
    ) : (
      null
    )
  };
}

export default SaveStoreInput;