import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import ContentEditable from 'react-contenteditable';

import withTheme from '../../hoc/withTheme';
import withHover from '../../hoc/withHover';
import { AddButton, RemoveButton } from '../Button';
import { borderStyles, hoverInputAndFocus } from '../../shared';

const styles = StyleSheet.create({
  container: {
    marginTop: '1em',
    position: 'relative',
    ...borderStyles,
  },
  title: {
    textTransform: 'uppercase',
    marginTop: 0,
    ...hoverInputAndFocus,
  },
  list: {
    listStyle: 'none',
    fontSize: '1.1em',
    paddingLeft: 0,
  },
  listItem: {
    position: 'relative',
  },
  btn: {
    border: 'none',
    borderRadius: '50%',
    position: 'absolute',
    right: 0,
    top: 0,
    height: 24,
    width: 24,
  },
  addBtn: {
    background: 'green',
    color: '#fff',
  },
  removeBtn: {
    background: 'red',
    color: '#fff',
  },
  highlighted: {
    ...hoverInputAndFocus,
  }
});


const ListItemWithHover = withHover(class ListItem extends Component<any> {

  handleUpdateName = (id:number, key: string) => (evt: any) => {
    this.props.update(id, key, evt.target.value);
  };

  handleUpdateValue = (id: number, key: string) => (evt: any) => {
    this.props.update(id, key, evt.target.value);
  };

  render() {
    return (
      <li
        className={css(styles.listItem)}
      >
        <ContentEditable
          className={css(styles.highlighted)}
          html={this.props.name}
          onChange={this.handleUpdateName(this.props.id,'name')}
          style={{color: this.props.theme.color}}
          tagName='h4'
        />
        <ContentEditable
          className={css(styles.highlighted)}
          html={this.props.value}
          onChange={this.handleUpdateValue(this.props.id, 'value')}
          tagName='p'
        />
        { this.props.isHovered && (
          <RemoveButton
            onClick={() => this.props.remove(this.props.id)}
          />
        )}
      </li>
    )
  }
});

class PersonalDetails extends Component<any> {
  render() {
    const {
      addNewDetail,
      personal,
      updateHead,
      theme,
      updateDetail,
      removeDetail
    } = this.props;

    return (
      <div className={css(styles.container)}>
        <ContentEditable
          className={css(styles.title)}
          html={personal.head}
          onChange={updateHead}
          tagName='h3'
        />
        <ul className={css(styles.list)}>
          { personal.details.map((detail: any, id: number) => (
            <ListItemWithHover
              key={id}
              id={id}
              name={detail.name}
              value={detail.value}
              theme={theme}
              update={updateDetail}
              remove={removeDetail}
            />
          )) }
        </ul>
        { this.props.isHovered && (
          <AddButton
            onClick={addNewDetail}
          />
        )}
      </div>
    );
  }
}

export default withHover(withTheme(PersonalDetails));