import React, { Component } from 'react';
import { connect } from 'react-redux';

import Toolbar from '../../presentational/Toolbar';
import { Dispatch } from 'redux';
import { toggleContent, updateTheme } from '../../../store/actions/settings';
import { fontOptions } from './constants';

interface ReduxProps {
  settings: object,
  updateTheme: (category: string, value: number | string) => void;
  toggleContent: (category: string) => void;
}

class ToolbarContainer extends Component<ReduxProps> {
  render() {
    return (
      <Toolbar
        settings={this.props.settings}
        updateTheme={this.props.updateTheme}
        toggleContent={this.props.toggleContent}
        fontOptions={fontOptions}
      />
    );
  }
}

interface IState {
  settings: object,
}

const mapStateToProps = (state: IState) => {
  return {
    settings: state.settings,
  }
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    updateTheme: (category: string, value: number | string) => dispatch(updateTheme(category, value)),
    toggleContent: (category: string) => dispatch(toggleContent(category)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(ToolbarContainer);