import React, { Component } from 'react';

const withHover = (WrappedComponent: React.ComponentClass<any, any>, style: object = {}) => {
  return class Hover extends Component<any, any> {
    state = {
      isHovered: false,
    };

    handleMouseEnter = () => {
      this.setState({
        isHovered: true,
      })
    };

    handleMouseLeave = () => {
      this.setState({
        isHovered: false,
      })
    };

    resetHover = () => {
      this.setState({
        isHovered: false,
      })
    };

    render() {
      return (
        <div
          onMouseLeave={this.handleMouseLeave}
          onMouseEnter={this.handleMouseEnter}
          style={style}
        >
          <WrappedComponent
            {...this.props as any}
            isHovered={this.state.isHovered}
            resetHover={this.resetHover}
          />
        </div>
      );
    }
  };
};

export default withHover;