import React, { Component } from 'react';
import { connect } from 'react-redux';
import { css, StyleSheet } from 'aphrodite';
import { toast } from 'react-toastify';
import { loadCv } from '../../../store/actions/main';
import { loadLocalStorageItem } from '../../../store/utils/persistStateUtils';
import store from '../../../store';
import { Provider } from 'react-redux';
import generateCvLoadedNotif from './LastCvLoaded';

const styles = StyleSheet.create({
  paragraph: {
    fontSize: '0.8em',
  },
  btn: {
    backgroundColor: '#fff',
    border: 'none',
    color: 'steelblue',
    borderRadius: 5,
    cursor: 'pointer',
    padding: '0.5em 1em',
    marginBottom: '1em',
  }
});

interface OwnProps {
  projectName: string;
  loadCv: (cv: any) => void;
}


const mapDispatchToProps = (dispatch: any) => {
  return {
    loadCv: (data: object) => dispatch(loadCv(data)),
  }
};

class LoadCvPrompt extends Component<OwnProps> {
  handleLoadCv = () => {
    this.props.loadCv(loadLocalStorageItem(this.props.projectName));
    generateCvLoadedNotif(this.props.projectName);
  };

  render() {
    return (
        <div>
          <p className={css(styles.paragraph)}>
            Your last project <strong>{this.props.projectName}</strong> has been found. Load it?
          </p>
          <button className={css(styles.btn)} onClick={this.handleLoadCv}>Yes!</button>
        </div>
    );
  }
}

const Comp = connect(null, mapDispatchToProps)(LoadCvPrompt);

const generateLoadCvPrompt = (projectName: string) => toast.info(() => <Provider store={store}><Comp projectName={projectName} /></Provider>);

export default generateLoadCvPrompt;