import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';

const popUpAnimation = {
  from: {
    transform: 'scale(0.1)',
  },
  to: {
    transform: 'scale(1)',
  }
};

const animationProps = {
  animationName: [ popUpAnimation ],
  animationTimingFunction: 'ease-out',
  animationDuration: '0.3s',
};

const styles = StyleSheet.create({
  btn: {
    ...animationProps,
    border: 'none',
    borderRadius: '50%',
    cursor: 'pointer',
    position: 'absolute',
    right: 5,
    top: 5,
    height: 24,
    width: 24,
    transition: 'transform 0.3s',
    ':hover': {
      transform: 'rotate(360deg) scale(1.1)',
    }
  },
  addBtn: {
    background: 'green',
    color: '#fff',
  },
  removeBtn: {
    background: 'red',
    color: '#fff',
    right: 0,
  }
});

interface Props {
  onClick: () => void,
}

export class AddButton extends Component<Props> {
  render() {
    return (
      <button
        className={css(styles.btn, styles.addBtn)}
        onClick={this.props.onClick}
      >
        <i className="fas fa-plus" />
      </button>
    )
  }
}

export class RemoveButton extends Component<Props> {
  render() {
    return (
      <button
        className={css(styles.btn, styles.removeBtn)}
        onClick={this.props.onClick}
      >
        <i className="fas fa-minus" />
      </button>
    )
  }
}

export default {
  AddButton,
  RemoveButton,
}