import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import {
  updateCertificationHead,
  addNewCertificationDetail,
  updateCertificationDetail,
  removeCertificationDetail
} from '../../../store/actions/certifications';
import { Certification, State } from '../../../store/reducers/certifications';
import Bio from './Bio';
import Bullet from './Bullet';
import ChartWrapper from './ChartWrapper';

interface ReduxProps {
  certifications: State,
  updateHead: (name: string) => void;
  addNew: () => void;
  update: (id: number, key: string, value: string) => void;
  remove: (id: number) => void;
}

class AdditionalInfo extends Component<ReduxProps> {
  render() {
    const { certifications } = this.props;
    return (
      <Bio
        title={this.props.certifications.head}
        updateTitle={this.props.updateHead}
        addNew={this.props.addNew}
      >
        <ChartWrapper>
          {
            certifications.details.map((certification: Certification, id: number) => (
              <Bullet
                key={id}
                id={id}
                name={certification.name}
                update={this.props.update}
                remove={this.props.remove}
              />
            ))
          }
        </ChartWrapper>
      </Bio>
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    certifications: state.certifications,
  }
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    updateHead: (name: string) => dispatch(updateCertificationHead(name)),
    addNew: () => dispatch(addNewCertificationDetail()),
    update: (id: number, key: string, value: string) => dispatch(updateCertificationDetail(id, key, value)),
    remove: (id: number) => dispatch(removeCertificationDetail(id)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(AdditionalInfo);