export const ADD_NEW_CONTACT = 'ADD_NEW_CONTACT';
export const UPDATE_CONTACT_HEAD = 'UPDATE_CONTACT_HEAD';
export const UPDATE_CONTACT_DETAIL = 'UPDATE_CONTACT_DETAIL';
export const REMOVE_CONTACT_DETAIL = 'REMOVE_CONTACT_DETAIL';

interface ContactDetail {
  value?: string,
  iconClassName?: string,
}

export const addNewContactDetail = () => {
  const DEFAULT_DETAIL: ContactDetail = {
    value: 'Input a name',
    iconClassName: 'far fa-hand-pointer'
  };

  return {
    type: ADD_NEW_CONTACT,
    payload: DEFAULT_DETAIL,
  }
};

export const updateContactHead = (payload: string) => {
  return {
    type: UPDATE_CONTACT_HEAD,
    payload: payload,
  };
};

export const updateContactDetail = (id: number, key: string, value: string) => {
  return {
    type: UPDATE_CONTACT_DETAIL,
    id,
    key,
    value,
  }
};

export const removeContactDetail = (id: number) => {
  return {
    type: REMOVE_CONTACT_DETAIL,
    payload: id,
  };
};