export const ADD_NEW_CERTIFICATION = 'ADD_NEW_CERTIFICATION';
export const UPDATE_CERTIFICATIONS_HEAD = 'UPDATE_CERTIFICATIONS_HEAD';
export const UPDATE_CERTIFICATIONS_DETAIL = 'UPDATE_CERTIFICATIONS_DETAIL';
export const REMOVE_CERTIFICATIONS_DETAIL = 'REMOVE_CERTIFICATIONS_DETAIL';

interface CertificationDetail {
  name?: string;
  percentage?: number;
}

export const addNewCertificationDetail = () => {
  const DEFAULT_DETAIL: CertificationDetail = {
    name: 'A Certification',
    percentage: 0,
  };

  return {
    type: ADD_NEW_CERTIFICATION,
    payload: DEFAULT_DETAIL,
  }
};

export const updateCertificationHead = (payload: string) => {
  return {
    type: UPDATE_CERTIFICATIONS_HEAD,
    payload: payload,
  };
};

export const updateCertificationDetail = (id: number, key: string, value: string | number) => {
  return {
    type: UPDATE_CERTIFICATIONS_DETAIL,
    id,
    key,
    value,
  }
};

export const removeCertificationDetail = (id: number) => {
  return {
    type: REMOVE_CERTIFICATIONS_DETAIL,
    payload: id,
  };
};