import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import ContentEditable from 'react-contenteditable';
import withHover from '../../hoc/withHover';
import { AddButton } from '../Button';
import { borderStyles, hoverInputAndFocus } from '../../shared';

const styles = StyleSheet.create({
  container: {
    position: 'relative',
    marginTop: '1em',
    ...borderStyles,
  },
  title: {
    marginBottom: '1em',
    marginTop: '0em',
    textTransform: 'uppercase',
    ...hoverInputAndFocus,
  }
});

interface OwnProps {
  title: string;
  updateTitle: () => void;
  addNew: () => void;
}

interface HocProps {
  isHovered: boolean;
}

type Props = OwnProps & HocProps;

class Bio extends Component<Props> {
  render() {
    const { children, isHovered } = this.props;

    return (
      <div className={css(styles.container)}>
        <ContentEditable
          className={css(styles.title)}
          html={this.props.title}
          onChange={this.props.updateTitle}
          tagName={'h3'}
        />
        { children }
        { isHovered && (
          <AddButton
            onClick={this.props.addNew}
          />
        )}
      </div>
    );
  }
}

export default withHover(Bio);