import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import { toast } from 'react-toastify';

const styles = StyleSheet.create({
  paragraph: {
    fontSize: '0.8em',
  }
});

interface OwnProps {
  projectName: string;
}

class LastCvLoaded extends Component<OwnProps> {
  render() {
    return (
      <div>
        <p className={css(styles.paragraph)}>
          We loaded your last project <strong>{this.props.projectName}</strong>. You can still load other projects in the <strong>top bar</strong>.
        </p>
      </div>
    );
  }
}

const generateCvLoadedNotif = (projectName: string) => toast.success(() => <LastCvLoaded projectName={projectName} />);

export default generateCvLoadedNotif;