import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';

const popUpAnimation = {
  from: {
    transform: 'scale(0.9)',
  },
  to: {
    transform: 'scale(1)',
  }
};

const animationProps = {
  animationName: [ popUpAnimation ],
  animationTimingFunction: 'cubic-bezier(.22,.57,.35,1.46)',
  animationDuration: '0.3s',
  transformOrigin: 'top center',
};

const styles = StyleSheet.create({
  container: {
    ...animationProps,
    fontFamily: 'Arial',
    backgroundColor: '#333',
    border: '3px solid #333',
    borderRadius: 10,
    boxSizing: 'border-box',
    display: 'flex',
    alignItems: 'center',
    padding: '1em',
    top: '3.25em',
    position: 'absolute',
  },
  arrow: {
    position: 'absolute',
    borderStyle: 'solid',
    borderColor: 'transparent transparent #333 transparent',
    borderWidth: '10px',
    left: 'calc(50% - 10px)',
    top: -20,
  }
});

class DropdownWrapper extends Component {
  render() {
    return (
      <div className={css(styles.container)}>
        <div className={css(styles.arrow)} />
        { this.props.children }
      </div>
    );
  }
}

export default DropdownWrapper;