import {
  RESET_AVATAR_ROTATION,
  UPDATE_AVATAR_ROTATION,
  UPDATE_HEADING,
  UPDATE_PROFILE,
} from '../actions';

const DEFAULT_STATE = {
  heading: {
    name: 'Your name',
    position: 'Your position',
    avatar: '',
    rotation: 0,
  },
  profile: {
    head: 'Profile',
    body: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.'
  },
};

export default (state = DEFAULT_STATE, action: any): any => {
  switch (action.type) {
    case UPDATE_HEADING: {
      return {
        ...state,
        heading: {
          ...state.heading,
          ...action.payload,
        }
      }
    }
    case UPDATE_PROFILE: {
      return {
        ...state,
        profile: {
          ...state.profile,
          ...action.payload,
        },
      }
    }
    case UPDATE_AVATAR_ROTATION: {
      const increment = 90;
      const newRotation = state.heading.rotation + increment || increment;

      return {
        ...state,
        heading: {
          ...state.heading,
          rotation: newRotation,
        }
      }
    }
    case RESET_AVATAR_ROTATION: {
      return {
        ...state,
        heading: {
          ...state.heading,
          rotation: 0,
        }
      }
    }
    default: {
      return state;
    }
  }
};