import React, { Component, MouseEvent } from 'react';
import { connect } from 'react-redux';
import 'react-toastify/dist/ReactToastify.min.css'

import {
  areStatesSame,
  loadLocalStorageKeys,
  loadLocalStorageItem,
  saveLocalStorageItem,
  removeFromStorage,
  lastSavedKeyProp
} from '../../../store/utils/persistStateUtils';

import Menu from '../../presentational/Menu';
import FabButton from '../../presentational/FabButton';
import SaveStoreInput from '../../presentational/SaveStoreInput';
import { loadCv } from '../../../store/actions/main';
import { generateErrorNotif } from '../../presentational/toasts';
import generateSuccessNotif from '../../presentational/toasts/Success';
import generateLoadCvPrompt from '../../presentational/toasts/LoadCvPrompt';

interface Props {
  loadCv: (data: object) => void;
}

interface State {
  savedSettings: string[];
  inputOpen: boolean;
  menuOpen: boolean;
}

export interface Setting {
  name: string;
  iconClassName: string;
  onRemove: (name: string) => void;
  onLoad: (name: string) => void;
}

class LocalStorageConnect extends Component<Props, State> {
  state = {
    savedSettings: [],
    inputOpen: false,
    menuOpen: false,
  };

  public componentDidMount() {
    this.updateItemList();
    const lastSavedCv = loadLocalStorageItem(lastSavedKeyProp);
    const lastItem = lastSavedCv[lastSavedCv.length - 1];

    if (lastSavedCv && !areStatesSame(loadLocalStorageItem(lastItem))) {
      generateLoadCvPrompt(lastItem);
    }
  }

  private handleToggleInput = (evt: MouseEvent<HTMLButtonElement | HTMLDivElement>) => {
    if (evt.currentTarget === evt.target || evt.currentTarget.tagName === 'BUTTON') {
      this.setState({inputOpen: !this.state.inputOpen});
    }
  };

  private handleToggleMenu = () => {
    this.setState({ menuOpen: !this.state.menuOpen });
  };

  private updateItemList = () => {
    this.setState({
      savedSettings: loadLocalStorageKeys(),
    });
  };

  private handleLoadItem = (name: string) => {
    generateSuccessNotif('Project ' + name + ' is loaded successfully!');
    this.setState({ menuOpen: false});
    this.props.loadCv(loadLocalStorageItem(name));
  };

  private handleSaveItem = (name: string) => {
    saveLocalStorageItem(name);
    generateSuccessNotif('Project ' + name + ' is saved successfully!');
    this.updateItemList();
    this.setState({ inputOpen: false});
  };

  private handleRemoveIem = (name: string) => {
    removeFromStorage(name);
    generateErrorNotif('Project ' + name + ' is removed successfully!');
    this.updateItemList();
  };

  private addItemsProps = (settings: string[]): Setting[] => {
    return settings.map((setting: string) => ({
      name: setting,
      iconClassName: 'fa fa-trash',
      onRemove: this.handleRemoveIem,
      onLoad: this.handleLoadItem,
    }));
  };

  public render() {
    return (
      <div>
        <FabButton
          iconClassName='fas fa-save'
          onClick={this.handleToggleInput}
          style={{ backgroundColor: '#ff3259'}}
        />
        <SaveStoreInput
          open={this.state.inputOpen}
          onSave={this.handleSaveItem}
          onClose={this.handleToggleInput}
        />
        <FabButton
          iconClassName='fas fa-file-download'
          onClick={(this.handleToggleMenu)}
          style={{ backgroundColor: '#333'}}
        />
        <Menu
          items={this.addItemsProps(this.state.savedSettings)}
          open={this.state.menuOpen}
          onClose={this.handleToggleMenu}
        />
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    loadCv: (data: object) => dispatch(loadCv(data)),
  }
};

export default connect(null, mapDispatchToProps)(LocalStorageConnect);