import React, { Component } from 'react';
import { connect } from 'react-redux';

import {
  updateContactHead,
  addNewContactDetail,
  removeContactDetail,
  updateContactDetail
} from '../../../store/actions/contact';
import Contact from '../../presentational/Paper/Contact';

interface IProps {
  contact: {
    head: string,
    details: any[];
  },
  updatePersonal: (data: string) => void;
  addNewDetail: () => void;
  updateDetail: (id: number, key: string, value: string) => void;
  removeDetail: (id: number) => void;
}

class ContactContainer extends Component<IProps> {
  private handleChangeHead = (evt: any) => {
    this.props.updatePersonal(evt.target.value)
  };

  private handleAddDetail = () => {
    this.props.addNewDetail();
  };

  public render() {
    return (
      <Contact
        contact={this.props.contact}
        updateHead={this.handleChangeHead}
        addNew={this.handleAddDetail}
        update={this.props.updateDetail}
        remove={this.props.removeDetail}
      />
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    contact: state.contact,
  }
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    updatePersonal: (data: string) => dispatch(updateContactHead(data)),
    addNewDetail: () => dispatch(addNewContactDetail()),
    updateDetail: (id: number, key: string, value: string) => dispatch(updateContactDetail(id, key, value)),
    removeDetail: (id: number) => dispatch(removeContactDetail(id)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactContainer);