import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import ContentEditable from 'react-contenteditable';

import withTheme from '../../hoc/withTheme';
import Avatar from './Avatar';
import { borderStyles, hoverInputAndFocus } from '../../shared';

const styles = StyleSheet.create({
  heading: {
    display: 'flex',
    alignItems: 'center',
  },
  credentials: {
    padding: '1em',
    width: '100%',
    ...borderStyles,
  },
  name: {
    fontSize: '3em',
    margin: '5px 0',
    ...hoverInputAndFocus,
  },
  position: {
    textTransform: 'uppercase',
    fontSize: '1.5em',
    color: '#777',
    fontWeight: 100,
    ...hoverInputAndFocus,
  }
});

class Heading extends Component<any> {
  render() {
    const {
      avatarOn,
      heading,
      updateName,
      updatePosition,
      updateAvatar,
      updateRotation,
    } = this.props;

    return (
      <div className={css(styles.heading)}>
        { avatarOn && <Avatar updateAvatar={updateAvatar} avatarImage={heading.avatar} updateRotation={updateRotation} avatarRotation={heading.rotation} /> }
        <div className={css(styles.credentials)}>
          <ContentEditable
            className={css(styles.name)}
            html={heading.name} // innerHTML of the editable div
            onChange={updateName}
            style={{ color: this.props.theme.color }}
            tagName='h2'
          />
          <ContentEditable
            className={css(styles.position)}
            html={heading.position}
            onChange={updatePosition}
            tagName='h3'
          />
        </div>
      </div>
    );
  }
}

export default withTheme(Heading);