import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';

import ToPdfContainer from '../../containers/ToPdfContainer';

const styles = StyleSheet.create({
    container: {
        marginBottom: '1em',
        display: 'flex',
        justifyContent: 'center',
        position: 'fixed',
        width: '100%',
        zIndex: 9999,
    },
});

let lastScrollY = 0;
let ticking = false;

class ToolbarBottom extends Component {
    state = {
        bottomAdjustment: 0,
    };

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        const pixelFixed = 50;
        const maxScroll =
            Math.round(document.body.scrollHeight) -
            Math.round(document.documentElement.clientHeight);
        lastScrollY = window.scrollY;

        if (!ticking) {
            window.requestAnimationFrame(() => {
                this.setState({
                    bottomAdjustment: Math.max(
                        lastScrollY + pixelFixed - maxScroll,
                        0
                    ),
                });
                ticking = false;
            });
            ticking = true;
        }
    };
    render() {
        return (
            <div
                className={css(styles.container)}
                style={{ bottom: this.state.bottomAdjustment }}
                id="save-to-pdf-btn"
            >
                <ToPdfContainer />
            </div>
        );
    }
}

export default ToolbarBottom;
