import React, { Component } from 'react';
import { connect } from 'react-redux';

import Profile from '../../presentational/Paper/Profile';
import { updateProfile } from '../../../store/actions';

interface IProps {
  profile: {
    head: string,
    body: string;
  },
  updateProfile: (data: any) => void
}

class ProfileContainer extends Component<IProps> {

  private handleChangeHead = (evt: any) => {
    this.props.updateProfile({
      head: evt.target.value,
    })
  };

  private handleChangeBody = (evt: any) => {
    this.props.updateProfile({
      body: evt.target.value,
    })
  };

  public render() {
    return (
      <Profile
        profile={this.props.profile}
        updateHead={this.handleChangeHead}
        updateBody={this.handleChangeBody}
      />
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    profile: state.paper.profile,
  }
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    updateProfile: (data: any) => dispatch(updateProfile(data))
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(ProfileContainer);