import {
  UPDATE_CERTIFICATIONS_HEAD,
  ADD_NEW_CERTIFICATION,
  UPDATE_CERTIFICATIONS_DETAIL,
  REMOVE_CERTIFICATIONS_DETAIL
} from '../actions/certifications';

export interface Certification {
  name: string;
  [key: string]: string;
}

export interface State {
  head: string;
  details: Certification[];
}

interface Action {
  type: string;
  payload: any;
  id?: number;
  key?: string;
  value?: string;
}

const DEFAULT_STATE: State = {
  head: 'Additional Info',
  details: [
    { name: 'Certification, achievement...' },
  ]
};

export default (state: State = DEFAULT_STATE, action: Action) => {
  switch (action.type) {
    case ADD_NEW_CERTIFICATION: {
      return {
        ...state,
        details: state.details.concat(action.payload),
      }
    }
    case UPDATE_CERTIFICATIONS_HEAD: {
      return {
        ...state,
        head: action.payload.target.value,
      }
    }
    case UPDATE_CERTIFICATIONS_DETAIL: {
      const details: Certification[] = [ ...state.details ];

      if ((action.id || action.id === 0) && action.key && action.value) {
        details[action.id][action.key] = action.value;
      }

      return {
        ...state,
        details
      }
    }
    case REMOVE_CERTIFICATIONS_DETAIL: {
      return {
        head: state.head,
        details: state.details.filter((detail, id) => id !== action.payload)
      }
    }
    default: {
      return state;
    }
  }
};