import {
  ADD_NEW_CONTACT,
  REMOVE_CONTACT_DETAIL,
  UPDATE_CONTACT_HEAD,
  UPDATE_CONTACT_DETAIL
} from '../actions/contact';

type ContactItem = {
  value: string,
  iconClassName: string
  [key: string]: string;
};

interface State {
  head: string;
  details: ContactItem[];
}

const DEFAULT_STATE: State = {
  head: 'Contact',
  details: [
    { value: 'your-name@company.com', iconClassName: 'fas fa-envelope' },
    { value: '/your-profile', iconClassName: 'fab fa-linkedin-in' },
    { value: '+00 000 000 000', iconClassName: 'fas fa-phone' },
  ]
};

export default (state = DEFAULT_STATE, action: any): any => {
  switch (action.type) {
    case ADD_NEW_CONTACT: {
      return {
        ...state,
        details: state.details.concat(action.payload),
      }
    }
    case UPDATE_CONTACT_HEAD: {
      return {
        ...state,
        head: action.payload,
      }
    }
    case UPDATE_CONTACT_DETAIL: {
      const details: ContactItem[] = [ ...state.details ];

      details[action.id][action.key] = action.value;

      return {
        ...state,
        details
      }
    }
    case REMOVE_CONTACT_DETAIL: {
      return {
        head: state.head,
        details: state.details.filter((detail, id) => id !== action.payload)
      }
    }
    default: {
      return state;
    }
  }
};