import React, { Component } from 'react';

import ToolbarContainer from '../../containers/ToolbarContainer';
import PaperContainer from '../../containers/PaperContainer';
import withTheme from '../../hoc/withTheme';
import ToolbarBottom from '../../presentational/ToolbarBottom';
import Helmet from 'react-helmet';

interface Props {
    theme: any;
}

class Creator extends Component<Props> {
    render() {
        const { theme } = this.props;

        const style = {
            display: 'flex',
            alignItems: 'center',
            backgroundColor: theme.color,
            fontFamily: theme.fontFamily,
            padding: 'calc(2.59em * 2) 0',
            transition: 'background-color 1s',
        };

        return (
            <div
                style={{ ...style, flexDirection: 'column' }}
                id="base-content"
            >
                <Helmet>
                    <title>My CV - Creator</title>
                    <meta
                        name="description"
                        content="Here you can create your brand new CV! You can also save your work and visit the page later to continue!"
                    />
                </Helmet>

                <ToolbarContainer />
                <PaperContainer />
                <ToolbarBottom />
            </div>
        );
    }
}

export default withTheme(Creator);
