import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import Slider from 'react-rangeslider';
import 'react-rangeslider/lib/index.css';

import DropdownWrapper from '../DropdownWrapper';
import withTheme from '../../hoc/withTheme';

const styles = StyleSheet.create({
  container: {
    width: 100,
  },
  description: {
    color: '#fff',
    margin: '0 0 1em 0',
    fontSize: '0.7em',
  },
});

class FontSizeDropdown extends React.Component<any> {
  handleChange = (evt: any) => {
    this.props.updateTheme('fontSize', evt)
  };

  formatTooltip = (value: number) => () => {
    switch (true) {
      case value > 1.05: {
        return 'XL';
      }
      case value > 1: {
        return 'L';
      }
      case value < 0.75: {
        return 'XS'
      }
      case value < 0.85: {
        return 'S';
      }
      default: {
       return 'M';
      }
    }
  };

  render() {
    return (
      <DropdownWrapper>
        <div className={css(styles.container)}>
          <p className={css(styles.description)}>Font size</p>
          <Slider
            value={this.props.theme.fontSize}
            min={0.7}
            max={1.1}
            step={0.01}
            format={this.formatTooltip(this.props.theme.fontSize)}
            onChange={this.handleChange}
          />
        </div>
      </DropdownWrapper>
    );
  }
}

export default withTheme(FontSizeDropdown);