import React, { Component } from 'react';
import { connect } from 'react-redux';
import html2canvas from 'html2canvas';
import JsPDF from 'jspdf';
import SaveButton from '../../presentational/SaveButton';
import { Dispatch } from 'redux';

interface Props {
    userName: string;
    sendMsg?: () => void;
    className?: string;
}

class ToPdfContainer extends Component<Props> {
    imageToPdf = (imageBase64: string) => {
        const imageWidth = 210; // A4 width in mm
        const imageHeight = 297; // A4 height in mm

        const pdfDoc = new JsPDF();
        pdfDoc.addImage(imageBase64, 'png', 0, 0, imageWidth, imageHeight);
        pdfDoc.save(`${this.props.userName} - CV.pdf`);
    };

    handleSavePdf = () => {
        const htmlElement: HTMLElement | null = document.querySelector(
            '#my-cv'
        );
        const imageQuality = 1; // 0 to 1

        const btn: any = document.querySelector('.fa-trophy');

        setTimeout(() => {
            btn.click();
            btn.click();
        }, 0);

        setTimeout(() => {
            html2canvas(htmlElement as HTMLElement).then(
                (canvas: HTMLCanvasElement) => {
                    const image: string = canvas.toDataURL(
                        'image/jpeg',
                        imageQuality
                    );
                    this.imageToPdf(image);
                }
            );
        }, 100);
    };

    render() {
        return (
            <SaveButton
                iconClassName="far fa-file-pdf"
                onClick={this.handleSavePdf}
                text={'Print'}
            />
        );
    }
}

const mapStateToProps = (state: any) => {
    return {
        userName: state.paper.heading.name,
    };
};

const mapDispatchToProps = (dispatch: Dispatch) => {
    return {
        sendMsg: () => dispatch({ type: 'DUMMY_EVENT' }),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ToPdfContainer);
