import { css, StyleSheet } from 'aphrodite/no-important';

const jumpAnimationProps = {
  animationName: [{
    '0%': {
      transform: 'scale(1)',
    },
    '30%': {
      transform: 'scale(1.05)',
    },
    '100%': {
      transform: 'scale(1)',
    }
  }],
  animationDuration: '1000ms',
  animationFillMode: 'forwards',
  animationDelay: '500ms',
};

const fadeInAnimationProps = {
  opacity: 0,
  animationName: [{
    '0%': {
      opacity: 0,
    },
    '100%': {
      opacity: 1,
    }
  }],
  animationDuration: '1000ms',
  animationFillMode: 'forwards',
  animationDelay: '1500ms',
};

const styles = StyleSheet.create({
  paperAnimation: {
    ...jumpAnimationProps,
  },
  sectionsAnimation: {
    ...fadeInAnimationProps,
  },
});

let loadedPaper: number = 0;
export const animatePaper = () => {
  loadedPaper +=1;
  if (loadedPaper <= 3) {
    return css(styles.paperAnimation);
  } else {
    return '';
  }
};

let loadedSections: number = 0;
export const animateSections = (maxCount: number) => {
  loadedSections +=1;
  if (loadedSections <= maxCount) {
    return css(styles.sectionsAnimation);
  } else {
    return '';
  }
};
