import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import ContentEditable from 'react-contenteditable';

import withTheme from '../../hoc/withTheme';
import { borderStyles, hoverInputAndFocus } from '../../shared';

interface IProps {
  profile: {
    head: string,
    body: string;
  },
  updateHead: (data: { head: string }) => void;
  updateBody: (data: { body: string }) => void;
}

const styles = StyleSheet.create({
  container: {
    ...borderStyles,
    marginTop: '1em',
  },
  title: {
    marginTop: '0',
    textTransform: 'uppercase',
    ...hoverInputAndFocus,
  },
  paragraph: {
    textAlign: 'justify',
    fontSize: '1.0em',
    ...hoverInputAndFocus,
  }
});

class Profile extends Component<IProps> {

  constructor(props: IProps) {
    super(props);
  }

  public render() {
    const {
      profile,
      updateHead,
      updateBody,
    } = this.props;

    return (
      <div className={css(styles.container)}>
        <ContentEditable
          className={css(styles.title)}
          html={profile.head} // innerHTML of the editable div
          onChange={updateHead}
          tagName='h3'
        />
        <ContentEditable
          className={css(styles.paragraph)}
          html={profile.body}
          onChange={updateBody}
          tagName='p'
        />
      </div>
    );
  }
}

export default withTheme(Profile);