import {
  ADD_PERSONAL_DETAIL,
  REMOVE_PERSONAL_DETAIL,
  UPDATE_PERSONAL_DETAIL,
  UPDATE_PERSONAL_HEAD
} from '../actions';

const DEFAULT_STATE = {
  head: 'Personal details',
  details: [
    { name: 'Nationality', value: 'Your nationality' },
    { name: 'Residence', value: 'City, Country' },
  ],
};

export default (state = DEFAULT_STATE, action: any): any => {
  switch (action.type) {
    case ADD_PERSONAL_DETAIL: {
      return {
        head: state.head,
        details: state.details.concat(action.payload)
      }
    }
    case UPDATE_PERSONAL_HEAD: {
      return {
        ...state,
        head: action.payload,
      }
    }
    case UPDATE_PERSONAL_DETAIL: {
      const details: any = [ ...state.details ];

      details[action.id][action.key] = action.value;

      return {
        ...state,
        details
      }
    }
    case REMOVE_PERSONAL_DETAIL: {
      return {
        head: state.head,
        details: state.details.filter((detail, id) => id !== action.payload)
      }
    }
    default: {
      return state;
    }
  }
};