import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import { Link } from 'react-router-dom';
import appStore from '../../../store';

const imageUrl =
    'https://images.unsplash.com/photo-1492145080082-3154cd0d402a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2850&q=80';

const styles = StyleSheet.create({
    container: {
        display: 'flex',
        color: '#777',
        paddingLeft: '10vw',
        height: '85vh',
        '@media screen and (max-width: 900px)': {
            paddingLeft: 0,
            justifyContent: 'center',
            color: '#555',
        },
    },
    heroText: {
        width: '50vw',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'baseline',
        flexShrink: 0,
        '@media screen and (max-width: 900px)': {
            zIndex: 1,
            alignItems: 'center',
            borderRadius: 10,
            textAlign: 'center',
            fontSize: '0.9em',
        },
    },
    heroImage: {
        borderTopLeftRadius: '25%',
        borderBottomLeftRadius: '50%',
        objectFit: 'cover',
        maxHeight: 'calc(90vh + 0.8em)',
        width: '40vw',
        opacity: 0.6,
        '@media screen and (max-width: 900px)': {
            position: 'absolute',
            width: '100%',
            borderRadius: 0,
            zIndex: -1,
            opacity: 0.3,
        },
    },
    features: {
        textTransform: 'uppercase',
        fontSize: '0.9em',
        fontWeight: 300,
    },
    actionTitle: {
        fontWeight: 300,
    },
    emp: {
        fontWeight: 800,
    },
    subTitle: {
        fontSize: '0.9em',
        fontWeight: 300,
    },
    btn: {
        border: 'none',
        borderRadius: 10,
        color: '#fff',
        cursor: 'pointer',
        display: 'inline-block',
        fontSize: '1.15em',
        margin: '1em 0',
        maxWidth: 250,
        padding: '1em',
        transition: 'transform 0.3s',
        ':hover': {
            transform: 'scale(1.05)',
        },
    },
    disabled: {
        cursor: 'normal',
        backgroundColor: 'red',
        opacity: 0.75,
        fontSize: '0.8em',
        ':hover': {
            transform: 'scale(1)',
        },
    },
});

class Landing extends Component {
    state = {
        windowWidth: 0,
    };

    componentWillMount(): void {
        this.setState({
            windowWidth: window.innerWidth,
        });
    }

    render() {
        const { windowWidth } = this.state;

        const actionColor = (cssCategory: string) => ({
            [cssCategory]: appStore.getState().settings.theme.color,
        });

        return (
            <div className={css(styles.container)}>
                <div className={css(styles.heroText)}>
                    <h3 className={css(styles.features)}>
                        Easy. Free. Beautiful
                    </h3>
                    <h1 className={css(styles.actionTitle)}>
                        Create a great resume <br />
                        and apply for a{' '}
                        <span
                            style={actionColor('color')}
                            className={css(styles.emp)}
                        >
                            dream
                        </span>{' '}
                        job
                    </h1>
                    <h3 className={css(styles.subTitle)}>
                        No registration required, you can start working
                        instantly.
                    </h3>
                    {windowWidth >= 900 ? (
                        <Link to="/easy-cv/creator">
                            <button
                                className={css(styles.btn)}
                                style={actionColor('background')}
                            >
                                Get started. For free
                            </button>
                        </Link>
                    ) : (
                        <button className={css(styles.btn, styles.disabled)}>
                            This app was designed for tablets and desktops. Your
                            device has probably too small screen width to
                            conveniently create a resume.{' '}
                            <strong>
                                Please open this site in something bigger ;-)
                            </strong>
                        </button>
                    )}
                </div>
                <img
                    height="1000px"
                    alt="man sitting and resting on top of the hill"
                    className={css(styles.heroImage)}
                    src={imageUrl}
                />
            </div>
        );
    }
}

export default Landing;
