import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import {
  updateSkillHead,
  addNewSkillDetail,
  updateSkillDetail,
  removeSkillDetail
} from '../../../store/actions/skills';
import Bio from './Bio';
import BarChart from './BarChart';
import ChartWrapper from './ChartWrapper';
import { State as SkillsState, Skill } from '../../../store/reducers/skills';

interface Props {
  skills: SkillsState;
  updateHead: (name: string) => void;
  addNew: () => void;
  update: (id: number, key: string, value: string | number) => void;
  remove: (id: number) => void;
}

class Skills extends Component<Props> {
  render() {
    return (
      <Bio
        title={this.props.skills.head}
        updateTitle={this.props.updateHead}
        addNew={this.props.addNew}
      >
        <ChartWrapper>
          {
            this.props.skills.details.map((skill: Skill, id) => (
              <BarChart
                key={id}
                id={id}
                name={skill.name}
                percentage={skill.percentage}
                update={this.props.update}
              />
            ))
          }
        </ChartWrapper>
      </Bio>
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    skills: state.skills,
  }
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    updateHead: (name: string) => dispatch(updateSkillHead(name)),
    addNew: () => dispatch(addNewSkillDetail()),
    update: (id: number, key: string, value: string | number) => dispatch(updateSkillDetail(id, key, value)),
    remove: (id: number) => dispatch(removeSkillDetail(id)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Skills);