export const ADD_NEW_SKILL = 'ADD_NEW_SKILL';
export const UPDATE_SKILLS_HEAD = 'UPDATE_SKILLS_HEAD';
export const UPDATE_SKILLS_DETAIL = 'UPDATE_SKILLS_DETAIL';
export const REMOVE_SKILLS_DETAIL = 'REMOVE_SKILLS_DETAIL';

interface SkillDetail {
  name?: string;
  percentage?: number;
}

export const addNewSkillDetail = () => {
  const DEFAULT_DETAIL: SkillDetail = {
    name: 'A skill',
    percentage: 0,
  };

  return {
    type: ADD_NEW_SKILL,
    payload: DEFAULT_DETAIL,
  }
};

export const updateSkillHead = (payload: string) => {
  return {
    type: UPDATE_SKILLS_HEAD,
    payload: payload,
  };
};

export const updateSkillDetail = (id: number, key: string, value: string | number) => {
  return {
    type: UPDATE_SKILLS_DETAIL,
    id,
    key,
    value,
  }
};

export const removeSkillDetail = (id: number) => {
  return {
    type: REMOVE_SKILLS_DETAIL,
    payload: id,
  };
};