import React, { Component } from 'react';
import ContentEditable from 'react-contenteditable';
import { css, StyleSheet } from 'aphrodite';
import withTheme from '../../hoc/withTheme';
import withHover from '../../hoc/withHover';
import { AddButton, RemoveButton } from '../Button';
import { borderStyles, hoverInputAndFocus } from '../../shared';

const styles = StyleSheet.create({
  container: {
    marginTop: '1em',
    position: 'relative',
    ...borderStyles,
  },
  title: {
    marginTop: '0em',
    textTransform: 'uppercase',
    ...hoverInputAndFocus,
  },
  list: {
    listStyle: 'none',
    paddingLeft: 0,
  },
  listItem: {
    position: 'relative',
    display: 'flex',
    alignItems: 'center',
    margin: '0.75em 0',
  },
  icon: {
    alignItems: 'center',
    border: 'none',
    borderRadius: '50%',
    color: '#fff',
    cursor: 'pointer',
    display: 'flex',
    flexShrink: 0,
    justifyContent: 'center',
    marginRight: '0.5em',
    position: 'relative',
    height: 36,
    width: 36,
    outline: 'none',
  },
  menuContainer: {
    position: 'relative',
  },
  menu: {
    backgroundColor: 'black',
    listStyle: 'none',
    padding: 0,
    position: 'absolute',
    width: '20vh',
    zIndex: 2,
    boxShadow: 'rgba(0,0,0,0.3) 5px 5px 20px 1px',
  },
  menuBtn: {
    alignItems: 'center',
    background: '#333',
    border: 'none',
    cursor: 'pointer',
    color: '#fff',
    fill: '#fff',
    fontSize: '.95em',
    display: 'flex',
    justifyContent: 'space-between',
    padding: '1em 1em',
    width: '100%',
    ':hover': {
      backgroundColor: '#555',
    }
  },
  selectedMenuBtn: {
    backgroundColor: '#444',
  },
  contactValue: {
    wordBreak: 'break-all',
    fontSize: '0.85em',
    ...hoverInputAndFocus,
  },
  btn: {
    border: 'none',
    borderRadius: '50%',
    position: 'absolute',
    right: 0,
    top: 0,
    height: 24,
    width: 24,
  },
  addBtn: {
    background: 'green',
    color: '#fff',
  },
  removeBtn: {
    background: 'red',
    color: '#fff',
  },
  capture: {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100vw',
    height: '100vh',
    zIndex: 1,
  }
});

interface MenuProps {
  open: boolean;
  close: () => void;
  update: (value: string) => void;
  iconClassName: string;
}

const Menu: React.FunctionComponent<MenuProps> = ({ close, open, update, iconClassName }) => {
  const DEFAULT_MENU_ITEMS = [
    { name: 'Mail', iconClassName: 'fas fa-envelope' },
    { name: 'Phone', iconClassName: 'fas fa-phone' },
    { name: 'Linkedin', iconClassName: 'fab fa-linkedin-in' },
    { name: 'Website', iconClassName: 'fas fa-globe' },
    { name: 'Twitter', iconClassName: 'fab fa-twitter' }
  ];

  const handleClick = (value: string) => {
    update(value);
  };

  return open ? (
    <div className={css(styles.menuContainer)}>
      <ul className={css(styles.menu)}>
        {
          DEFAULT_MENU_ITEMS.map(MENU_ITEM => (
            <li key={MENU_ITEM.name}>
              <button className={css(styles.menuBtn, iconClassName === MENU_ITEM.iconClassName && styles.selectedMenuBtn)} onClick={() => handleClick(MENU_ITEM.iconClassName)}>
                <i className={MENU_ITEM.iconClassName} />
                {MENU_ITEM.name}
              </button>
            </li>
          ))
        }
      </ul>
      <div className={css(styles.capture)} onClick={close} />
    </div>
  ) : (
    <div />
  )
};

interface ContactItemProps {
  className: string;
  iconClassName: string;
  id: number;
  value: string;
  style: object;
  isHovered?: boolean;
  resetHover: () => void;
  update: (id: number, key: string, value: string) => void;
  remove: (id: number) => void;
}

interface State {
  isClicked: boolean,
}

const ContactItemWithHover = withHover(class ContactItem extends Component<ContactItemProps, State> {
  state = {
    isClicked: false,
  };

  handleUpdateIcon = (value: string) => {
    this.handleClose();
    this.props.update(this.props.id, 'iconClassName', value);
  };

  handleClose = () => {
    this.setState({
      isClicked: false,
    });
  };

  handleUpdateValue = (id: number, key: string) => (evt: any) => {
    this.props.update(id, key, evt.target.value);
  };

  handleOpenMenu = () => {
    this.props.resetHover();

    this.setState({
      isClicked: !this.state.isClicked,
    });
  };

  render() {
    const {
      className,
      iconClassName,
      id,
      value,
      style,
      isHovered,
      remove
    } = this.props;

    return (
      <li className={className}>
        <button className={css(styles.icon)} style={style} onClick={this.handleOpenMenu}>
          <i
            className={iconClassName}
          />
        </button>
        <Menu
          open={this.state.isClicked}
          update={this.handleUpdateIcon}
          close={this.handleClose}
          iconClassName={iconClassName}
        />
        <ContentEditable
          html={value}
          className={css(styles.contactValue)}
          onChange={this.handleUpdateValue(id, 'value')}
          tagName={'span'}
        />
        { isHovered && (
          <RemoveButton
            onClick={() => remove(id)}
          />
        )}
      </li>
    );
  };
});

class Contact extends Component<any> {
  render() {
    return (
      <div className={css(styles.container)}>
        <ContentEditable
          className={css(styles.title)}
          html={this.props.contact.head}
          onChange={this.props.updateHead}
          tagName={'h3'}
        />
        <ul className={css(styles.list)}>
          { this.props.contact.details.map((contactItem: ContactItemProps, id: number) => (
            <ContactItemWithHover
              iconClassName={`${contactItem.iconClassName}`}
              key={id}
              id={id}
              className={css(styles.listItem)}
              style={{ backgroundColor: this.props.theme.color }}
              value={contactItem.value}
              update={this.props.update}
              remove={this.props.remove}
            />
          )) }
        </ul>
        { this.props.isHovered && (
          <AddButton
            onClick={this.props.addNew}
          />
        )}
      </div>
    );
  }
}

export default withHover(withTheme(Contact));