import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';

import withTheme from '../../hoc/withTheme';
import { Skill } from '../../../store/reducers/skills';
import ContentEditable from 'react-contenteditable';
import { hoverInputAndFocus } from '../../shared';

const styles = StyleSheet.create({
  container: {
    width: 'calc(50% - 0.5em)',
    display: 'flex',
    alignItems: 'center',
    marginBottom: '0.5em',
    marginRight: '0.5em',
    zIndex: 1,
  },
  chartContainer: {
    cursor: 'pointer',
    height: '0.45em',
    marginRight: '0.5em',
    position: 'relative',
    width: '50%',
    flexShrink: 0,
  },
  chart: {
    height: '100%',
    position: 'absolute',
    transition: 'width 0.3s',
    zIndex: 0,
  },
  completed: {
    zIndex: 1,
  },
  mask: {
    backgroundColor: '#ddd',
    width: '100%',
  },
  label: {
    fontSize: '0.9em',
    ...hoverInputAndFocus,
  }
});

interface OwnProps {
  update: (id: number, key: string, value: string | number) => void;
  id: number;
}

interface ReduxProps {
  theme: any,
}

type Props = OwnProps & ReduxProps & Skill;

class BarChart extends Component<Props> {
  handlePercentageChange = () => {
    const KEY_NAME = 'percentage';
    this.props.update(this.props.id, KEY_NAME, 0);
  };

  handleNameChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const KEY_NAME = 'name';
    this.props.update(this.props.id, KEY_NAME, evt.target.value);
  };

  render() {
    return (
      <div className={css(styles.container)}>
        <div className={css(styles.chartContainer)} onClick={this.handlePercentageChange}>
          <div className={css(styles.chart, styles.completed)} style={{
            backgroundColor: this.props.theme.color,
            width: `${this.props.percentage}%`,
          }} />
          <div className={css(styles.chart, styles.mask)} />
        </div>
        <ContentEditable
          className={css(styles.label)}
          html={this.props.name}
          onChange={this.handleNameChange}
          tagName={'label'}
        />
      </div>
    );
  }
}

export default withTheme(BarChart);