import React, { Component } from 'react';
import { connect } from 'react-redux';

import PersonalDetails from '../../presentational/Paper/PersonalDetails';
import {
  updatePersonalHead,
  addNewPersonalDetail,
  removePersonalDetail,
  updatePersonalDetail
} from '../../../store/actions';

interface IProps {
  personal: {
    head: string,
    details: any[];
  },
  updatePersonal: (data: string) => void;
  addNewDetail: () => void;
  updateDetail: (id: number, key: string, value: string) => void;
  removeDetail: (id: number) => void;
}

class PersonalDetailsContainer extends Component<IProps> {
  private handleChangeHead = (evt: any) => {
    this.props.updatePersonal(evt.target.value)
  };

  private handleAddDetail = () => {
    this.props.addNewDetail();
  };

  public render() {
    return (
      <PersonalDetails
        personal={this.props.personal}
        updateHead={this.handleChangeHead}
        addNewDetail={this.handleAddDetail}
        updateDetail={this.props.updateDetail}
        removeDetail={this.props.removeDetail}
      />
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    personal: state.personal,
  }
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    updatePersonal: (data: string) => dispatch(updatePersonalHead(data)),
    addNewDetail: () => dispatch(addNewPersonalDetail()),
    updateDetail: (id: number, key: string, value: string) => dispatch(updatePersonalDetail(id, key, value)),
    removeDetail: (id: number) => dispatch(removePersonalDetail(id)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(PersonalDetailsContainer);