import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { IStoreState } from '../../../types';
import { updateTheme } from '../../../store/actions/settings';

const mapStateToProps = (state: IStoreState) => {
  return {
    theme: state.settings.theme,
  }
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    updateTheme: (category: string, value: string | number) => dispatch(updateTheme(category, value)),
  }
};

const withTheme = (WrappedComponent: any) => {
  return connect(
    mapStateToProps,
    mapDispatchToProps,
  )(class WithTheme extends Component<any, any> {
    render() {
      return (
        <WrappedComponent
          {...this.props}
          theme={this.props.theme}
          updateTheme={this.props.updateTheme}
        />
      )
    }
  });
};

export default withTheme;