import React, { Component } from 'react';
import { connect } from 'react-redux';

import Heading from '../../presentational/Paper/Heading';
import { updateAvatarRotation, updateHeading } from '../../../store/actions';

interface IProps {
  avatarOn: boolean;
  heading: {
    head: string,
    body: string;
  };
  updateHeading: (data: any) => void;
  updateRotation: (actionName: 'reset' | MouseEvent) => void;
}

class ProfileContainer extends Component<IProps> {

  private handleChangeName = (evt: any) => {
    this.props.updateHeading({
      name: evt.target.value,
    });
  };

  private handleChangePosition = (evt: any) => {
    this.props.updateHeading({
      position: evt.target.value,
    });
  };

  private handleAvatarImageChange = (imageEncoded: string) => {
    this.props.updateHeading({
      avatar: imageEncoded,
    });
  };

  public render() {
    return (
      <Heading
        heading={this.props.heading}
        updateName={this.handleChangeName}
        updatePosition={this.handleChangePosition}
        updateAvatar={this.handleAvatarImageChange}
        updateRotation={this.props.updateRotation}
        avatarOn={this.props.avatarOn}
      />
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    heading: state.paper.heading,
    avatarOn: state.settings.contentOn.avatar,
  }
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    updateHeading: (data: any) => dispatch(updateHeading(data)),
    updateRotation: (actionName: 'reset' | MouseEvent) => dispatch(updateAvatarRotation(actionName))
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(ProfileContainer);