import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';

import withTheme from '../../hoc/withTheme';
import withHover from '../../hoc/withHover';
import { RemoveButton } from '../Button';
import ContentEditable from 'react-contenteditable';
import { hoverInputAndFocus } from '../../shared';

interface ThemeProps {
  theme: any;
}

interface HoverProps {
  isHovered: boolean;
}

interface OwnProps {
  name: string;
  id: number;
  update: (id: number, key: string, value: string) => void;
  remove: (id: number) => void;
}

type Props = ThemeProps & HoverProps & OwnProps;

const styles = StyleSheet.create({
  container: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: '0.5em',
    position: 'relative',
  },
  label: {
    marginLeft: '0.5em',
    ...hoverInputAndFocus
  }
});

class Bullet extends Component<Props> {
  handleChangeName = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const KEY_NAME = 'name';
    this.props.update(this.props.id, KEY_NAME, evt.target.value);
  };

  render() {
    return (
      <div className={css(styles.container)}>
        <i className="fas fa-circle" style={{ color: this.props.theme.color }} />
        <ContentEditable
          className={css(styles.label)}
          html={this.props.name}
          onChange={this.handleChangeName}
          tagName={'label'}
        />
        {
          this.props.isHovered && (
            <RemoveButton
              onClick={() => this.props.remove(this.props.id)}
            />
          )
        }
      </div>
  );
  }
}

export default withHover(withTheme(Bullet), { width: '50%' });