import {
  ADD_NEW_WORK_EXPIERENCE, REMOVE_WORK_EXPERIENCE_DETAIL,
  UPDATE_WORK_EXPERIENCE_DETAIL,
  UPDATE_WORK_EXPERIENCE_HEAD
} from '../actions/workExperience';

export interface Experience {
  title: string;
  organization: string;
  period: string;
  description: string;
  [key: string]: string;
}

export interface State {
  head: string;
  details: Experience[];
}

interface Action {
  type: string;
  payload: any;
  key?: string;
  id?: number;
  value?: string;
}

const DEFAULT_STATE: State = {
  head: 'Work Experience',
  details: [
    {
      title: 'Your job title',
      organization: 'Your company',
      period: 'From - To',
      description: 'Your tasks, duties, projects and other interesting stuff'
    },
  ]
};

export default (
  state: State = DEFAULT_STATE,
  action: Action
) => {
  switch (action.type) {
    case ADD_NEW_WORK_EXPIERENCE: {
      return {
        ...state,
        details: state.details.concat(action.payload),
      }
    }
    case UPDATE_WORK_EXPERIENCE_HEAD: {
      return {
        ...state,
        head: action.payload,
      }
    }
    case UPDATE_WORK_EXPERIENCE_DETAIL: {
      const details: Experience[] = [ ...state.details ];

      if ((action.id || action.id === 0) && action.key && action.value) {
        details[action.id][action.key] = action.value;
      }

      return {
        ...state,
        details
      }
    }
    case REMOVE_WORK_EXPERIENCE_DETAIL: {
      return {
        head: state.head,
        details: state.details.filter((detail, id) => id !== action.payload)
      }
    }
    default: {
      return state;
    }
  }
}