import React, { Component } from 'react';
import { connect } from 'react-redux';

import { Experience, State } from '../../../store/reducers/workExperience';
import {
  updateWorkExperienceHead,
  addNewWorkExperienceDetail,
  updateWorkExperienceDetail,
  removeWorkExperienceDetail
} from '../../../store/actions/workExperience';
import Bio from './Bio';
import BioTile from './BioTile';
import withTheme from '../../hoc/withTheme';
import { Dispatch } from 'redux';

const BioTileWithTheme = withTheme(BioTile);

interface ReduxProps {
  workExperience: State;
  updateHead: (name: string) => void;
  addNew: () => void;
  update: (id: number, key: string, value: string) => void;
  remove: (id: number) => void;
}

class WorkExperience extends Component<ReduxProps> {
  private handleUpdateHead = (evt: React.ChangeEvent<HTMLInputElement>) => {
    this.props.updateHead(evt.target.value);
  };

  public render() {
    return (
      <Bio
        title={this.props.workExperience.head}
        updateTitle={this.handleUpdateHead}
        addNew={this.props.addNew}
      >
        {
          this.props.workExperience.details.map((workExperience: Experience, id: number) => (
            <BioTileWithTheme
              key={id}
              id={id}
              title={workExperience.title}
              organization={workExperience.organization}
              period={workExperience.period}
              description={workExperience.description}
              update={this.props.update}
              remove={this.props.remove}
            />
          ))
        }
      </Bio>
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    workExperience: state.workExperience,
  }
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    updateHead: (payload: string) => dispatch(updateWorkExperienceHead(payload)),
    addNew: () => dispatch(addNewWorkExperienceDetail()),
    update: (id: number, key: string, value: string) => dispatch(updateWorkExperienceDetail(id, key, value)),
    remove: (id: number) => dispatch(removeWorkExperienceDetail(id)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(WorkExperience);