import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';

import DropdownWrapper from '../DropdownWrapper';
import withTheme from '../../hoc/withTheme';

const styles = StyleSheet.create({
  list: {
    fontFamily: 'Arial',
    listStyle: 'none',
    padding: 0,
    margin: 0,
  },
  description: {
    color: '#fff',
    margin: '0 0 1em 0',
    fontSize: '0.7em',
  },
  listItem: {
    margin: 0,
  },
  button: {
    background: 'none',
    border: 'none',
    color: '#fff',
    cursor: 'pointer',
    fontSize: '1.05em',
    margin: '0 -1.05em',
    padding: '1em 0',
    outline: 'none',
    width: 150,
    transition: '0.3s background-color',
    ':hover': {
      backgroundColor: '#555',
    },
  },
  selected: {
    backgroundColor: '#444',
  }
});

const fontsAvailable = [
  'Arial',
  'Book Antiqua',
  'Calibri',
  'Georgia',
  'Helvetica',
  'Trebuchet MS',
  'Roboto'
];

const ListItem: React.FunctionComponent<any> = ({ name, onClick, selectedFont }) => {
  return (
    <li className={css(styles.listItem)}>
      <button className={css(styles.button, selectedFont === name && styles.selected )} onClick={onClick}>{name}</button>
    </li>
  )
};

class FontDropdown extends Component<any> {
  render() {
    return (
      <DropdownWrapper>
        <ul className={css(styles.list)}>
          <p className={css(styles.description)}>Select font:</p>
          { fontsAvailable.map(font => (
            <ListItem
              key={font}
              name={font}
              selectedFont={this.props.theme.fontFamily}
              onClick={() => this.props.updateTheme('fontFamily', font)}
            />
          ))}
        </ul>
      </DropdownWrapper>
    );
  }
}

export default withTheme(FontDropdown);