export const UPDATE_THEME = 'UPDATE_THEME';
export const TOGGLE_CONTENT_VISIBILITY = 'TOGGLE_CONTENT_VISIBILITY';

export const updateTheme = (category: string, value: string | number) => {
  return {
    type: UPDATE_THEME,
    category,
    value
  }
};

export const toggleContent = (category: string) => {
  return {
    type: TOGGLE_CONTENT_VISIBILITY,
    category,
  }
};