import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import { toast } from 'react-toastify';

const styles = StyleSheet.create({
  paragraph: {
    fontSize: '0.8em',
  }
});

interface OwnProps {
  message: string;
}

class SuccessToast extends Component<OwnProps> {
  render() {
    return (
      <div>
        <p className={css(styles.paragraph)}>
          {this.props.message}
        </p>
      </div>
    );
  }
}

const generateSuccessNotif = (message: string) => toast.success(() => <SuccessToast message={message} />);

export default generateSuccessNotif;