export const ADD_NEW_WORK_EXPIERENCE = 'ADD_NEW_WORK_EXPIERENCE';
export const UPDATE_WORK_EXPERIENCE_HEAD = 'UPDATE_WORK_EXPERIENCE_HEAD';
export const UPDATE_WORK_EXPERIENCE_DETAIL = 'UPDATE_WORK_EXPERIENCE_DETAIL';
export const REMOVE_WORK_EXPERIENCE_DETAIL = 'REMOVE_WORK_EXPERIENCE_DETAIL';

interface WorkExperienceDetail {
  title?: string;
  organization?: string;
  period?: string;
  description?: string;
}

export const addNewWorkExperienceDetail = () => {
  const DEFAULT_DETAIL: WorkExperienceDetail = {
    title: 'Input your title',
    organization: 'Company you worked in',
    period: 'From - To',
    description: 'Your tasks, duties, projects and other interesting stuff'
  };

  return {
    type: ADD_NEW_WORK_EXPIERENCE,
    payload: DEFAULT_DETAIL,
  }
};

export const updateWorkExperienceHead = (payload: string) => {
  return {
    type: UPDATE_WORK_EXPERIENCE_HEAD,
    payload: payload,
  };
};

export const updateWorkExperienceDetail = (id: number, key: string, value: string) => {
  return {
    type: UPDATE_WORK_EXPERIENCE_DETAIL,
    id,
    key,
    value,
  }
};

export const removeWorkExperienceDetail = (id: number) => {
  return {
    type: REMOVE_WORK_EXPERIENCE_DETAIL,
    payload: id,
  };
};