import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import ContentEditable from 'react-contenteditable';

import { EducationProp } from '../../../store/reducers/educationExperience';
import { Experience } from '../../../store/reducers/workExperience';
import withTheme from '../../hoc/withTheme';
import withHover from '../../hoc/withHover';
import { RemoveButton } from '../Button';
import { hoverInputAndFocus } from '../../shared';

const styles = StyleSheet.create({
  container: {
    display: 'flex',
    position: 'relative',
  },
  decoration: {
    position: 'relative',
    top: 6.5,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    marginRight: '0.5em',
    paddingLeft: 6.5,
  },
  decorationLine: {
    backgroundColor: '#ccc',
    height: '100%',
    position: 'relative',
    transform: 'translateY(0px)',
    width: 2,
  },
  icon: {

  },
  title: {
    marginBottom: 0,
    marginTop: 0,
    ...hoverInputAndFocus,
  },
  subTitle: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  content: {
    flexGrow: 1,
    paddingBottom: '0.5em',
  },
  inputHover: {
    ...hoverInputAndFocus,
    margin: '0.75em 0 0 0'
  }
});

interface OwnProps {
  title: string;
  organization: string;
  period: string;
  description: string;
  id: number;
}

interface ReduxProps {
  theme: any;
  update: (id: number, key: string, value: string) => void;
  remove: (id: number) => void;
}

interface HocProps {
  isHovered: boolean;
}

type Props = Experience & EducationProp & ReduxProps & OwnProps & HocProps;

class BioTile extends Component<Props> {
  handleChangeTitle = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const KEY_NAME = 'title';
    this.props.update(this.props.id, KEY_NAME, evt.target.value);
  };

  handleChangeOrganization = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const KEY_NAME = 'organization';
    this.props.update(this.props.id, KEY_NAME, evt.target.value);
  };

  handleChangePeriod = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const KEY_NAME = 'period';
    this.props.update(this.props.id, KEY_NAME, evt.target.value);
  };

  handleChangeDescription = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const KEY_NAME = 'description';
    this.props.update(this.props.id, KEY_NAME, evt.target.value);
  };

  render() {
    return (
      <div className={css(styles.container)}>
        <div className={css(styles.decoration)}>
          <i className={`far fa-dot-circle`} style={{ color: this.props.theme.color }}/>
          <div className={css(styles.decorationLine)}/>
        </div>
        <div className={css(styles.content)}>
          <ContentEditable
            className={css(styles.title)}
            html={this.props.title}
            style={{ color: this.props.theme.color }}
            onChange={this.handleChangeTitle}
            tagName={'h4'}
          />
          <div className={css(styles.subTitle)}>
            <ContentEditable
              className={css(styles.inputHover)}
              html={this.props.organization}
              onChange={this.handleChangeOrganization}
              tagName={'span'}
            />
            <ContentEditable
              className={css(styles.inputHover)}
              html={this.props.period}
              onChange={this.handleChangePeriod}
              tagName={'span'}
            />
          </div>
          { this.props.description && <ContentEditable
            className={css(styles.inputHover)}
            html={this.props.description}
            onChange={this.handleChangeDescription}
            tagName={'p'}
          /> }
        </div>
        { this.props.isHovered && (
          <RemoveButton
            onClick={() => this.props.remove(this.props.id)}
          />
        )}
      </div>
    );
  }
}

export default withHover(withTheme(BioTile));