import React, { Component } from 'react';
import { connect } from 'react-redux';

import Bio from './Bio';
import BioTile from './BioTile';
import withTheme from '../../hoc/withTheme';
import { EducationProp, State } from '../../../store/reducers/educationExperience';
import {
  updateEducationExperienceHead,
  addNewEducationExperienceDetail,
  updateEducationExperienceDetail,
  removeEducationExperienceDetail,
} from '../../../store/actions/educationExperience';
import { Dispatch } from 'redux';

interface ReduxProps {
  education: State;
  updateHead: (name: string) => void;
  addNew: () => void;
  update: (id: number, key: string, value: string) => void;
  remove: (id: number) => void;
}

const BioTileWithTheme = withTheme(BioTile);

class Education extends Component<ReduxProps> {
  private handleUpdateHead = (evt: React.ChangeEvent<HTMLInputElement>) => {
    this.props.updateHead(evt.target.value);
  };

  render() {
    return (
      <Bio
        title={this.props.education.head}
        updateTitle={this.handleUpdateHead}
        addNew={this.props.addNew}
      >
        {
          this.props.education.details.map((education: EducationProp, id: number) => (
            <BioTileWithTheme
              key={id}
              id={id}
              title={education.title}
              organization={education.organization}
              period={education.period}
              // description={education.description}
              update={this.props.update}
              remove={this.props.remove}
            />
          ))
        }
      </Bio>
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    education: state.educationExperience,
  }
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    updateHead: (name: string) => dispatch(updateEducationExperienceHead(name)),
    addNew: () => dispatch(addNewEducationExperienceDetail()),
    update: (id: number, key: string, value: string) => dispatch(updateEducationExperienceDetail(id, key, value)),
    remove: (id: number) => dispatch(removeEducationExperienceDetail(id)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Education);