import {
  UPDATE_EDUCATION_EXPERIENCE_HEAD,
  ADD_NEW_EDUCATION_EXPIERENCE,
  UPDATE_EDUCATION_EXPERIENCE_DETAIL,
  REMOVE_EDUCATION_EXPERIENCE_DETAIL
} from '../actions/educationExperience';

export interface EducationProp {
  title: string;
  organization: string;
  period: string;
  description: string;
  [key: string]: string;
}

export interface State {
  head: string;
  details: EducationProp[];
}

interface Action {
  type: string;
  payload: any;
  id?: number;
  key?: string;
  value?: string;
}

const DEFAULT_STATE: State = {
  head: 'Education',
  details: [
    {
      title: 'Your Degree',
      organization: 'Your School / University',
      period: 'From - To',
      description: 'Your tasks, duties, projects and other interesting stuff'
    },
  ]
};

export default (
  state: State = DEFAULT_STATE,
  action: Action
) => {
  switch (action.type) {
    case ADD_NEW_EDUCATION_EXPIERENCE: {
      return {
        ...state,
        details: state.details.concat(action.payload),
      }
    }
    case UPDATE_EDUCATION_EXPERIENCE_HEAD: {
      return {
        ...state,
        head: action.payload,
      }
    }
    case UPDATE_EDUCATION_EXPERIENCE_DETAIL: {
      const details: EducationProp[] = [ ...state.details ];

      if ((action.id || action.id === 0) && action.key && action.value) {
        details[action.id][action.key] = action.value;
      }

      return {
        ...state,
        details
      }
    }
    case REMOVE_EDUCATION_EXPERIENCE_DETAIL: {
      return {
        head: state.head,
        details: state.details.filter((detail, id) => id !== action.payload)
      }
    }
    default: {
      return state;
    }
  }
}