import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';

const styles = StyleSheet.create({
  chartContainer: {
    display: 'flex',
    flexWrap: 'wrap',
  }
});

interface Props {
  style?: object;
}

class ChartWrapper extends Component<Props> {
  render() {
    return (
      <div className={css(styles.chartContainer)} style={this.props.style}>
        { this.props.children }
      </div>
    );
  }
}

export default ChartWrapper;