import { AnyAction } from 'redux';
import { ISettings } from '../../types'
import { UPDATE_THEME, TOGGLE_CONTENT_VISIBILITY } from '../actions/settings';

const initialState: ISettings = {
  theme: {
    color: '#00bcd4',
    fontFamily: 'Roboto',
    fontSize: 0.95,
  },
  contentOn: {
    avatar: true,
    profile: true,
    personal: true,
    skills: true,
    certifications: true,
  }
};

export default (state = initialState, action: AnyAction) => {
  switch (action.type) {
    case UPDATE_THEME: {
      return {
        ...state,
        theme: {
          ...state.theme,
          [action.category]: action.value,
        }
      }
    }
    case TOGGLE_CONTENT_VISIBILITY: {
      return {
        ...state,
        contentOn: {
          ...state.contentOn,
          [action.category]: !state.contentOn[action.category],
        }
      }
    }
    default: {
      return state;
    }
  }
};