import React from 'react';
import { css, StyleSheet } from 'aphrodite';
import { Setting } from '../../containers/LocalStorageConnect/LocalStorageConnect';

interface MenuProps {
  open: boolean;
  items: any;
  onClose: () => void;
}

const fadeInAnimation = {
  from: {
    backgroundColor: 'rgba(0,0,0,0)',
  },
  to: {
    backgroundColor: 'rgba(255,255,255,0.5)',
  }
};

const jumpAnimation = {
  '0%': {
    transform: 'scale(0.95)',
  },
  '30%': {
    transform: 'scale(1.05)',
  },
  '100%': {
    transform: 'scale(1)',
  }
};

const styles = StyleSheet.create({
  container: {
    animationName: [ fadeInAnimation ],
    animationDuration: '0.5s',
    animationFillMode: 'forwards',
    top: 0,
    bottom: 0,
    left: 0,
    right: 0,
    position: 'fixed',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 9999,
  },
  menu: {
    animationName: [ jumpAnimation ],
    animationDuration: '0.4s',
    animationFillMode: 'forwards',
    backgroundColor: '#333',
    listStyle: 'none',
    padding: 0,
  },
  innerContainer: {
    maxHeight: '50vh',
    overflowY: 'auto',
  },
  menuItem: {
    display: 'flex',
    width: 250,
    border: 'none',
    ':hover button': {
      backgroundColor: '#555',
    },
    ':hover button[type="button"]': {
      fontSize: '0.9em',
    },
    ':hover button[type="button"]:hover': {
      backgroundColor: '#ff3259',
    }
  },
  btn: {
    backgroundColor: '#333',
    border: 'none',
    color: '#fff',
    fill: '#fff',
    cursor: 'pointer',
    transition: '0.3s background-color, 0.3s font-size',
    outline: 'none',
  },
  trashBtn: {
    fontSize: '0em',
    padding: '0 1em',
    width: '20%',
  },
  projectBtn: {
    display: 'flex',
    fontSize: '0.9em',
    justifyContent: 'space-between',
    padding: '1em 1em',
    width: '80%',
    textAlign: 'left',
    zIndex: 9999,
  },
  cancelBtn: {
    backgroundColor: '#999',
    justifyContent: 'center',
    textAlign: 'center',
    width: '100%',
    ':hover': {
      backgroundColor: '#777',
    },
  }
});

const Menu: React.FunctionComponent<MenuProps> = ({ open, items, onClose }) => {
  return open ? (
    <div className={css(styles.container)} onClick={onClose}>
      <ul className={css(styles.menu)}>
        <div className={css(styles.innerContainer)}>
        {
          items.map((item: Setting) => (
            <li className={css(styles.menuItem)} key={item.name}>
              <button className={css(styles.btn, styles.projectBtn)} onClick={() => item.onLoad(item.name)}>
                {item.name}
              </button>
              <button type="button" className={css(styles.btn, styles.trashBtn)} onClick={() => item.onRemove(item.name)}>
                <i className={item.iconClassName} />
              </button>
            </li>
          ))
        }
        </div>
        <li>
          <button className={css(styles.btn, styles.projectBtn, styles.cancelBtn)} onClick={onClose}>
            Cancel
          </button>
        </li>
      </ul>
    </div>
  ) : (
    null
  )
};

export default Menu;