import {
  ADD_NEW_SKILL,
  REMOVE_SKILLS_DETAIL,
  UPDATE_SKILLS_DETAIL,
  UPDATE_SKILLS_HEAD
} from '../actions/skills';

export interface Skill {
  name: string;
  percentage: number;
  [key: string]: string | number;
}

export interface State {
  head: string;
  details: Skill[];
}

interface Action {
  type: string;
  payload: any;
  id?: number;
  key?: string;
  value?: string | number;
}

const DEFAULT_STATE: State= {
  head: 'Skills',
  details: [
    { name: 'A first skill', percentage: 100 },
    { name: 'A second skill', percentage: 75 },
  ]
};

export default (state: State = DEFAULT_STATE, action: Action) => {
  switch (action.type) {
    case ADD_NEW_SKILL: {
      return {
        ...state,
        details: state.details.concat(action.payload),
      }
    }
    case UPDATE_SKILLS_HEAD: {
      return {
        ...state,
        head: action.payload.target.value,
      }
    }
    case UPDATE_SKILLS_DETAIL: {
      const details: Skill[] = [ ...state.details ];

      if ((action.id || action.id === 0) && action.key && (action.value || action.value === 0)) {
        if (action.key === 'percentage') {
          details[action.id][action.key] < 100
            ? details[action.id][action.key] = details[action.id][action.key] + 25
            : details[action.id][action.key] = 0;
        } else {
          details[action.id][action.key] = action.value;
        }
      }

      return {
        ...state,
        details
      }
    }
    case REMOVE_SKILLS_DETAIL: {
      return {
        head: state.head,
        details: state.details.filter((detail, id) => id !== action.payload)
      }
    }
    default: {
      return state;
    }
  }
};