import appStore from '../index';
export const lastSavedKeyProp = 'lastKeySaved';

export const saveState = (name: string): void => {
  try {
    const state = appStore.getState();
    const serializedState = JSON.stringify(state);
    localStorage.setItem(lastSavedKeyProp, name);
    localStorage.setItem(name, serializedState);
  } catch {
    return undefined;
  }
};

export const loadLocalStorageKeys = (): string[] => {
 try {
   return Object
     .keys(localStorage)
     .filter(key => key !== lastSavedKeyProp); //we don't want to display this utility to client
 } catch {
   return [];
 }
};

export const loadLocalStorageItem = (name: string) => {
  try {
    return JSON.parse(localStorage.getItem(name) || '');
  } catch(err) {
    return localStorage.getItem(name) || '';
  }
};

export const saveLocalStorageItem = (name: string): void => {
  try {
    const state = appStore.getState();
    const serializedState = JSON.stringify(state);
    const lastSavedKeys = loadLocalStorageItem(lastSavedKeyProp);

    if (!lastSavedKeys) {
      localStorage.setItem(lastSavedKeyProp, JSON.stringify([ name ]));
    } else {
      localStorage.setItem(lastSavedKeyProp, JSON.stringify(lastSavedKeys.concat(name)));
    }

    localStorage.setItem(name, serializedState);

  } catch {
    return undefined;
  }
};

export const removeFromStorage = (name: string): void => {
  try {
    const lastSavedKeys = loadLocalStorageItem(lastSavedKeyProp);
    localStorage.setItem(lastSavedKeyProp, JSON.stringify(lastSavedKeys.filter((key: string) => key !== name)));
    localStorage.removeItem(name);
  } catch {
    return undefined;
  }
};

let initiallyLoaded = false;
export const areStatesSame = (state: object) => {
  const appStateJson = JSON.stringify(appStore.getState());
  const newStateJSon = JSON.stringify(state);

  if (!initiallyLoaded) {
    initiallyLoaded = true;
    return appStateJson === newStateJSon;
  }

  return initiallyLoaded;
};