import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import { Link, NavLink } from 'react-router-dom';
import withTheme from '../../hoc/withTheme';

const logo = require('../../../assets/logo.jpg');
// const logo = 'https://placeholder.com/wp-content/uploads/2018/10/placeholder.com-logo1.jpg';

const styles = StyleSheet.create({
  container: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: '0.5em',
  },
  logo: {
    width: 150,
    height: 40,
    backgroundSize: 'contain',
    backgroundBlendMode: 'luminosity',
  },
  nav: {
    width: '80%',
    display: 'flex',
    justifyContent: 'flex-end',
  },
  link: {
    width: '10vh',
    display: 'inline-block',
    color: '#222',
    margin: '0 1em',
    '@media screen and (max-width: 900px)': {
      display: 'none',
    }
  },
  activeLink: {
    fontWeight: 800,
  }
});

type Path = { label: string, path: string };

const paths: Path[] = [
  { label: 'Home', path: '/easy-cv/' },
  { label: 'Creator', path: '/easy-cv/creator' },
  { label: 'Privacy', path: '/easy-cv/privacy' }
];

interface Props {
  theme: any;
}

class TopBar extends Component<Props> {
  render() {
    return (
      <div className={css(styles.container)} id="header">
        <Link to='/'>
          <div className={css(styles.logo)} style={{ backgroundImage: `url(${logo})`, backgroundColor: this.props.theme.color }} />
        </Link>
        <nav className={css(styles.nav)}>
          { paths.map((path: Path) => (
            <NavLink exact to={path.path} key={path.path} activeClassName={css(styles.activeLink)}>
              <span className={css(styles.link)}> {path.label} </span>
            </NavLink>
          ))}
        </nav>
      </div>
    );
  }
}

export default withTheme(TopBar);
