export const ADD_NEW_EDUCATION_EXPIERENCE = 'ADD_NEW_EDUCATION_EXPIERENCE';
export const UPDATE_EDUCATION_EXPERIENCE_HEAD = 'UPDATE_EDUCATION_EXPERIENCE_HEAD';
export const UPDATE_EDUCATION_EXPERIENCE_DETAIL = 'UPDATE_EDUCATION_EXPERIENCE_DETAIL';
export const REMOVE_EDUCATION_EXPERIENCE_DETAIL = 'REMOVE_EDUCATION_EXPERIENCE_DETAIL';

interface EducationExperienceDetail {
  title?: string;
  organization?: string;
  period?: string;
  description?: string;
}

export const addNewEducationExperienceDetail = () => {
  const DEFAULT_DETAIL: EducationExperienceDetail = {
    title: 'Your degree',
    organization: 'School, college, university',
    period: 'From - To',
    description: 'Your tasks, duties, projects and other interesting stuff'
  };

  return {
    type: ADD_NEW_EDUCATION_EXPIERENCE,
    payload: DEFAULT_DETAIL,
  }
};

export const updateEducationExperienceHead = (payload: string) => {
  return {
    type: UPDATE_EDUCATION_EXPERIENCE_HEAD,
    payload: payload,
  };
};

export const updateEducationExperienceDetail = (id: number, key: string, value: string) => {
  return {
    type: UPDATE_EDUCATION_EXPERIENCE_DETAIL,
    id,
    key,
    value,
  }
};

export const removeEducationExperienceDetail = (id: number) => {
  return {
    type: REMOVE_EDUCATION_EXPERIENCE_DETAIL,
    payload: id,
  };
};