import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';

import Creator from './pages/Creator';
import Landing from './pages/Landing';
import Privacy from './pages/Privacy';
import TopBar from './presentational/TopBar';
import Footer from './presentational/Footer';
import Helmet from 'react-helmet';

class Routes extends Component {
    render() {
        return (
            <Switch>
                <Route exact path="/easy-cv/" component={Landing} />
                <Route exact path="/easy-cv/creator" component={Creator} />
                <Route exact path="/easy-cv/privacy" component={Privacy} />
            </Switch>
        );
    }
}

class App extends Component {
    render() {
        return (
            <div>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>My CV - Home</title>
                    <meta
                        name="description"
                        content="A free and easy to use CV creator"
                    />
                    <link rel="canonical" href="https://easy-cv.com" />
                </Helmet>
                <TopBar />
                <Routes />
                <Footer />
            </div>
        );
    }
}

export default App;
