export const UPDATE_HEADING: string = 'UPDATE_HEADING';
export const UPDATE_PROFILE: string = 'UPDATE_PROFILE';

export const UPDATE_PERSONAL_HEAD: string = 'UPDATE_PERSONAL_HEAD';
export const UPDATE_PERSONAL_DETAIL: string = 'UPDATE_PERSONAL_DETAUL';
export const ADD_PERSONAL_DETAIL: string = 'ADD_PERSONAL_DETAIL';
export const REMOVE_PERSONAL_DETAIL: string = 'REMOVE_PERSONAL_DETAUL';

export const UPDATE_AVATAR_ROTATION: string = 'UPDATE_AVATAR_ROTATION';
export const RESET_AVATAR_ROTATION: string = 'RESET_AVATAR_ROTATION';


interface Profile {
  head?: string;
  body?: string;
}

export const updateProfile = (payload: Profile) => {
  return {
    type: UPDATE_PROFILE,
    payload: payload,
  }
};


interface Heading {
  name?: string;
  position?: string;
  avatar?: string;
}

export const updateHeading = (payload: Heading) => {
  return {
    type: UPDATE_HEADING,
    payload: payload,
  }
};


interface PersonalDetail {
  name?: string,
  value?: string,
}

export const addNewPersonalDetail = () => {
  const DEFAULT_DETAIL: PersonalDetail = {
    name: 'Input a name',
    value: 'Input a value'
  };

  return {
    type: ADD_PERSONAL_DETAIL,
    payload: DEFAULT_DETAIL,
  }
};

export const updatePersonalHead = (payload: string) => {
  return {
    type: UPDATE_PERSONAL_HEAD,
    payload: payload,
  };
};

export const updatePersonalDetail = (id: number, key: string, value: string) => {
  return {
    type: UPDATE_PERSONAL_DETAIL,
    id,
    key,
    value,
  }
};

export const removePersonalDetail = (id: number) => {
  return {
    type: REMOVE_PERSONAL_DETAIL,
    payload: id,
  }
};

type OptionalParam = 'reset' | MouseEvent;
export const updateAvatarRotation = (text: OptionalParam) => {
  switch (text) {
    case 'reset': {
      return {
        type: RESET_AVATAR_ROTATION,
      }
    }
    default: {
      return {
        type: UPDATE_AVATAR_ROTATION,
      }
    }
  }
};